import React from 'react';
import {Link} from 'react-router-dom';
import {flowignore} from '../flowignore.js';

import TextField from 'material-ui/TextField';
import Subheader from 'material-ui/Subheader';
import injectTapEventPlugin from 'react-tap-event-plugin';

injectTapEventPlugin();



class Header extends React.Component {
  constructor(props) {
    super(props);
    //this.state = HeaderStore.getState();
    this.state = {flowignore_str : ""} 
    this.onChange = this.onChange.bind(this);
  }

  componentDidMount() {
    //HeaderStore.listen(this.onChange);
  }

  componentWillUnmount() {
    //HeaderStore.unlisten(this.onChange);
  }

  onChange(state) {
    this.setState(state);
  }

/*eslint-disable*/
  render() {
    return (
      <div className="HeadBarMain flexbox-container flexbox-col">
        <div className="flexbox-adjust">
	<TextField
	      hintText="Separate Packages by Space"
	      floatingLabelText="Package Flow Maker"
	      multiLine={true}
	      fullWidth={true}	
	      onKeyUp={(e) => {this.startFlow(e)}}
              style={{ fontSize: 25,padding:20 }}
	 />
        </div>
        <div className="flexbox-spread">
          <Subheader style={{ fontSize: 20 }}>Copy the below Regex</Subheader>
          <div className="flowignore_str">{this.state.flowignore_str}</div>
        </div> 
      </div>
    );
  }
/*eslint-enable*/
  startFlow(e){
   let val = e.target.value;
   this.setState({ flowignore_str: flowignore(val.split(" ")) })
  }
}

export default Header;



