var matchOperatorsRe = /[|\\{}()[\]^$+*?.]/g;

const escapeStringRegexp = function (str) {
  if (typeof str !== 'string') {
    throw new TypeError('Expected a string');
  }

  return str.replace(matchOperatorsRe, '\\$&');
};

export function flowignore(packageNames) {
  const tree = createCharacterTree(packageNames);
  return printCharacterTree(tree);
}

function createCharacterTree(words) {
  const tree = new Map();

  words.forEach(word => {
    addWord(tree, Array.from(word));
  });

  return tree;
}

function addWord(tree, wordChars) {
  if (wordChars.length === 0) {
    return;
  }

  const [char] = wordChars;
  let child = undefined;

  if (tree.has(char)) {
    child = tree.get(char);
  } else {
    child = new Map();
    tree.set(char, child);
  }

  addWord(child, wordChars.slice(1));
}

function printCharacterTree(tree) {
  const rows = treeToRows(tree, []);
  return rows.map(formatRow).join("\n");
}

function treeToRows(tree, previousChars) {
  const isLeaf = tree.size === 0;

  if (isLeaf) {
    const name = escapeStringRegexp(previousChars.join(""));
    return [`${name}.+`];
  }

  const nameSoFar = escapeStringRegexp(previousChars.join(""));
  const followingChars = escapeCharacterSet(Array.from(tree.keys()).sort().join(""));

  return [`${nameSoFar}[^${followingChars}].*`].concat(...Array.from(tree.entries()).sort().map(([char, child]) => treeToRows(child, previousChars.concat(char))));
}

function escapeCharacterSet(string) {
  return escapeStringRegexp(string).replace(/-/g, "\\-");
}

function formatRow(row) {
  return `<PROJECT_ROOT>/node_modules/${row}`;
} 



